import React, {Component} from 'react'
import Link from 'gatsby-link'
import Img from 'gatsby-image'
import image1 from '../assets/photos/A.I.A photo shooting (17).jpg'

const images = [
  {
    "id" : 1,
    "src" : require('../assets/photos/A.I.A photo shooting (17).jpg')
  },
  {
    "id" : 2,
    "src" : require('../assets/photos/A.I.A photo shooting (23).jpg' )
  },
  {
    "id" : 3,
    "src" : require('../assets/photos/A.I.A photo shooting (57).jpg' )
  }
]

class Photos extends Component {

  render() {
        return (
          <div>
          <p>{this.props.msg}</p>
          {this.props.images.map( (x) => {
            console.log('../'+x.node.relativePath)
            return (
                <div key = {x.node.id}>
                <p>This is {x.node.absolutePath}</p>
                <img src = {x.node.relativePath} />
              </div>
              )
          }
          )}
          </div>
        )
  }

}



const GalleryPage = ({data}) => {
    console.log(data.allFile.edges);
    return(
    <div>
      <h1>Hi from the second page </h1>
      <div>
        <h1>Hello gatsby-image</h1>
        
      </div>
      <p>Welcome to Gallery</p>
      <Photos images={data.allFile.edges} msg="Hi" />
      <img src= {image1} />
      <Link to="/">Go back to the homepage</Link>
    </div>
    )
}

export default GalleryPage

export const myquery = graphql`
query MyPhotos {

  allFile(filter:{
    extension:{eq:"jpg"}
  }) {
    edges {
      node {
        id
        absolutePath
        relativePath
        dir
        name
        }
      }
    }
    
  site {
    id
    host
  }

}
`



